.block .block-source {
  width: 100%;
  box-sizing: border-box;

  display: flex;
  flex-direction: row;
  position: relative;

  .source-code-main,
  .cell-status {
    background: #f5f5f5 !important;
    border: solid #e0e0e0 !important;
  }
  .source-code-main {
    margin: 0 0 0 0;
    padding: 0.75rem 2rem;
    box-sizing: border-box !important;
    border-width: 1px 1px 0 !important;
    flex: 1;

    counter-reset: line;
    line-height: 1.6;
  }
  .source-markdown {
    // Style tables automatically
    table {
      border-collapse: collapse;
      td, th {
        padding: 0.5rem;
      }
      &, td, th {

        border: 1px solid grey;
      }
    }
  }
  .cell-status {
    display: flex;
    outline: none;
    padding: 0 0.5rem;
    border-width: 0 1px 1px !important;

    & > * {
      padding: 3px;
      border: none;
      font-size: inherit;
      vertical-align: middle;
      display: inline !important;
    }
    .cell-type-btn {
      margin-left: auto;
    }
  }
}
